import { NextApiRequest, NextApiResponse } from 'next';
import { prisma } from '@/lib/prisma'; // 假设您使用 Prisma 作为 ORM

export default async function handler(
  req: NextApiRequest,
  res: NextApiResponse
) {
  if (req.method !== 'GET') {
    return res.status(405).json({ message: '方法不允许' });
  }

  try {
    const { id } = req.query;

    // 从数据库获取视频的价格信息
    const videoPricing = await prisma.videoPricing.findUnique({
      where: {
        videoId: id as string,
      },
      select: {
        singleEpisodePrice: true,
        memberSinglePrice: true,
        memberAllEpisodesPrice: true,
        vvvipPrice: true,
      },
    });

    if (!videoPricing) {
      // 如果没有找到特定视频的价格信息，返回默认价格
      return res.status(200).json({
        singleEpisodePrice: 5.00,      // 默认单集价格
        memberSinglePrice: 3.00,       // 默认会员单集价格
        memberAllEpisodesPrice: 20.00,  // 默认会员全集价格
        vvvipPrice: 50.00,             // 默认 VVVIP 价格
      });
    }

    // 返回找到的价格信息
    console.log(videoPricing);
    return res.status(200).json(videoPricing);

  } catch (error) {
    console.error('获取价格信息时发生错误:', error);
    return res.status(500).json({ 
      message: '获取价格失败',
      error: error instanceof Error ? error.message : '未知错误'
    });
  }
}
